<template>
<transition name="slide-fade">
	<div>
		<div class="cc-top">
			<div class="cc-name">{{job.name}}</div>
			<div class="cc-price">{{job.payStr}}</div>
			<div class="cc-time flexstart">
				<span>更新： {{job.updateTime}}</span>
				<span>浏览： {{job.readNum}}人</span>
			</div>
		</div>
		<div class="cc-top">
			<div class="cc-lr flexstart">
				<div class="cc-l">职位：</div>
				<div class="cc-r">{{job.cateName}}</div>
			</div>
			<div class="cc-lr flexstart">
				<div class="cc-l">要求：</div>
				<div class="cc-r">招{{job.num}}人 / {{job.educationStr}} / {{job.experienceStr}}</div>
			</div>
			<div class="cc-lr flexstart">
				<div class="cc-l">福利：</div>
				<div class="cc-r flexstart">
					<span  v-for="(item1,index1) in job.welfares" :key="index1">{{item1}}</span>
				</div>
			</div>
			<div class="cc-lr flexstart">
				<div class="cc-l">地址：</div>
				<div class="cc-r flexstart">{{job.city}} {{job.address}}</div>
			</div>
		</div>
		<div class="cc-company spacebetween">
			<div class="cc-l">
				<div class="cc-company1">{{company.name}}</div>
<!-- 				<div class="cc-name">联系人： {{company.linkman}}</div> -->
			</div>
			<div class="cc-r" v-if="company.hrMobile"><a :href="'tel:'+company.hrMobile"><img class="cc-logo" src="../assets/img/phone4.png" alt=""></a></div>
		</div>
		<div class="cc-top">
			<div class="cc-lr flexstart">
				<div class="cc-l">规模：</div>
				<div class="cc-r">{{company.sizeStr}}</div>
			</div>
			<div class="cc-lr flexstart">
				<div class="cc-l">行业：</div>
				<div class="cc-r">{{company.industry}}</div>
			</div>
			<div class="cc-lr flexstart">
				<div class="cc-l">地址：</div>
				<div class="cc-r">{{company.city}}{{company.address}}</div>
			</div>
		</div>
		<div class="cc-title">岗位要求</div>
		<div class="cc-des" v-html="job.explain"></div>
		<div class="cc-title">企业介绍</div>
		<div class="cc-des" v-html="company.content"></div>
		<div style="height: 50px;"></div>
		<div class="cc-button spacebetween" v-if="job.status!=3">
			<div class="cc-l" v-if="job.status==2" @click="del">删除</div>
			<div class="cc-r" v-if="job.status==2" @click="edit">编辑</div>
			<div class="cc-c" v-if="job.status==0" @click="down">暂停招聘</div>
			<div class="cc-c" v-if="job.status==1" @click="pub">重新发布</div>
		</div>
	</div>
</transition>
</template>
<script type="text/javascript">
import {Toast,Dialog} from 'vant';
export default {
	name: 'detail',
	components: {
		[Dialog.name]: Dialog,
		[Toast.name]: Toast,
	},
	data() {
		return {
			id:"",
			job:{},
			company:{},
		};
	},
	mounted() {
		this.id=this.$route.query.id;
		this.jobDetail();
	},
	methods: {
		edit(){
			this.$router.push({
				path:"/publish1?id="+this.id
			})
		},
		jobDetail() {
			var that=this;
			this.$http.get(`/company/jobDetail?jobId=${that.id}`).then(function (res) {
				if (res.code==10000) {
					that.job=res.data.job;
					that.company=res.data.company;
				}else{
					Toast.fail(res.msg);
				}
			}).catch(function () {
			});
		},
		del() {
			var that=this;
			Dialog.confirm({
				title: '提示',
				message: '您确定要删除该岗位吗？'
			}).then(() => {
				that.$http.get(`/company/jobDelete?jobId=${that.id}`).then(function (res) {
					if (res.code==10000) {
						Toast.success(res.msg);
						setTimeout(function(){ that.$router.back(); }, 1000);
					}else{
						Toast.fail(res.msg);
					}
				}).catch(function () {
				});
			}).catch(() => {
			});
		},
		down() {
			var that=this;
			Dialog.confirm({
				title: '提示',
				message: '您确定要暂停招聘吗？'
			}).then(() => {
				that.$http.get(`/company/jobClosed?jobId=${that.id}`).then(function (res) {
					if (res.code==10000) {
						Toast.success(res.msg);
						setTimeout(function(){ that.$router.back(); }, 1000);
					}else{
						Toast.fail(res.msg);
					}
				}).catch(function () {
				});
			}).catch(() => {
			});
		},
		pub() {
			var that=this;
			this.$http.get(`/company/jobOpen?jobId=${that.id}`).then(function (res) {
				if (res.code==10000) {
					Toast.success(res.msg);
					setTimeout(function(){ that.$router.back(); }, 1000);
				}else{
					Toast.fail(res.msg);
				}
			}).catch(function () {
			});
		},
	}
}
</script>
<style lang="scss" scoped>
.cc-top{
	padding:10px 15px;
	background:#fff;
	line-height: 24px;
	margin-bottom: 1px;
	.cc-name{
		font-size:18px;
		font-weight: bold;
		color:#333;
		padding:5px 0 14px;
	}
	.cc-price{
		font-size:20px;
		color:#FF8056;
		margin-bottom: 10px;
	}
	.cc-time{
		span{
			width: 172px;
			font-size:10px;
			color:#888;
		}

	}
	.cc-lr{
		padding-bottom: 5px;
		color:#666;
		.cc-l{
			width: 45px;
		}
		.cc-r{
			width: 250px;
			flex-wrap: wrap;
			span{
				height: 16px;
				padding:1px 3px;
				border: 1px solid #FF8056;
				color:#FF8056;
				margin:3px 4px 0 0;
				font-size:10px;
				line-height: 14px;
				border-radius:2px;
			}
		}
	}
}
.cc-company{
	padding:15px;
	background:#fff;
	line-height: 36px;
	margin: 10px 0 2px;
	.cc-l{
		width: 250px;
		.cc-company1{
			font-size:17px;
			color:#333;
		}
		.cc-name{
			color:#666;
		}
	}
	.cc-r{
		width: 95px;
		img{
			margin:0 auto;
			width: 36px;
			height: 36px;
		}
	}
}
.cc-title{
	background:#fff;
	line-height: 45px;
	padding-left: 15px;
	font-size: 16px;
	font-weight: 600;
	color:#333;
	margin: 10px 0 1px;
}
.cc-des{
	padding:15px;
	background:#fff;
	color:#888;
}
.cc-button{
	position: fixed;
	left: 0;
	bottom: 0;
	width: 375px;
	height: 60px;
	line-height: 44px;
	text-align: center;
	font-size:18px;
	padding:8px 16px;
	background:#fff;
	border-top: 1px solid #eee;
	.cc-l{
		width:122px;
		height:44px;
		border-radius:2px;
		border:1px solid rgba(34,155,172,1);
		color:rgba(33,154,171,1);
	}
	.cc-r{
		width:209px;
		height:44px;
		background:linear-gradient(314deg,rgba(30,151,168,1) 0%,rgba(52,174,191,1) 100%);
		border-radius:2px;
		color:rgba(255,255,255,1);
	}
	.cc-c{
		width:343px;
		height:44px;
		background:linear-gradient(314deg,rgba(30,151,168,1) 0%,rgba(52,174,191,1) 100%);
		border-radius:2px;
		color:rgba(255,255,255,1);
	}
}
</style>